let gContacts = _loadContacts();

function sort(arr) {
    return arr.sort((a, b) => {
        if (a.name.toLocaleLowerCase() < b.name.toLocaleLowerCase()) {
            return -1;
        }
        if (a.name.toLocaleLowerCase() > b.name.toLocaleLowerCase()) {
            return 1;
        }

        return 0;
    });
}

function filter(term) {
    term = term.toLocaleLowerCase();
    return gContacts.filter((contact) => {
        return (
            contact.name.toLocaleLowerCase().includes(term) ||
            contact.phone.toLocaleLowerCase().includes(term) ||
            contact.email.toLocaleLowerCase().includes(term)
        );
    });
}

function query(filterBy = null) {
    let contactsToReturn = gContacts;
    if (filterBy) {
        console.log('hi');
        contactsToReturn = filter(filterBy.term);
    }
    return Promise.resolve([...contactsToReturn]);
}

function getById(id) {
    const contact = gContacts.find((contact) => contact._id === id);
    return Promise.resolve({ ...contact });
}

function save(contact) {
    if (contact._id) _updateContact(contact);
    else _addContact(contact);
    storageService.store(CONTACT_KEY, gContacts);
    return Promise.resolve(contact);
}

function _addContact(contact) {
    contact._id = makeId();
    gContacts.push(contact);
}

function _updateContact(contact) {
    const idx = gContacts.findIndex(currContact => currContact._id === contact._id);
    gContacts.splice(idx, 1, contact);
}

function remove(id) {
    const idx = gContacts.findIndex((contact) => contact._id === id);
    gContacts.splice(idx, 1);
    if (!gContacts.length) gContacts = defaultContacts.slice();
    storageService.store(CONTACT_KEY, gContacts);
    return Promise.resolve();
}

function getEmptyContact() {
    return {
        name: '',
        email: '',
        phone: '',
    };
}

function _loadContacts() {
    let contacts = storageService.load(CONTACT_KEY);
    if (!contacts || !contacts.length) contacts = defaultContacts;
    storageService.store(CONTACT_KEY, contacts);
    return contacts;
}
